___CSS_LOADER_EXPORT___.push([module.id, `.header {
  display: flex;
  justify-content: center; /*        */
  align-items: center;
  background-color: white;
  padding: 10px;
}

.logo {
  width: 80px; /*   */
  height: auto; /*    */
  margin: 0 auto; /*   */
  border-radius: 50px;
}

.title {
  color: black;
  cursor: pointer;
  position: absolute; /*   */
  left: 60%; /*    */
  transform: translateX(-50%); /*      */
}
.loginButton {
  background-color: #534983; /*   */
  color: white; /*   */
  padding: 15px 30px; /* ,   */
  border: none; /*   */
  border-radius: 5px; /*   */
  cursor: pointer; /*      */
  text-align: center; /*    */
  transition: background 0.5s ease; /*    */
}

.loginButton:hover {
  background: linear-gradient(
    270deg,
    #ff0000,
    #ff7f00,
    #ffff00,
    #00ff00,
    #0000ff,
    #4b0082,
    #9400d3
  );
  background-size: 1400% 1400%;
  -webkit-animation: RainbowAnimation 3s ease infinite;
  -moz-animation: RainbowAnimation 3s ease infinite;
  animation: RainbowAnimation 3s ease infinite;
}

.myPage-btn {
  background-color: #42d538;
  color: white; /*   */
  padding: 15px 30px; /* ,   */
  border: none; /*   */
  border-radius: 5px; /*   */
  cursor: pointer; /*      */
}
.logoutButton {
  background-color: #42d538;
  color: white; /*   */
  padding: 15px 30px; /* ,   */
  border: none; /*   */
  border-radius: 5px; /*   */
  cursor: pointer; /*      */
  text-align: center; /*    */
  transition: background 0.5s ease; /*    */
}

@-webkit-keyframes RainbowAnimation {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}
@-moz-keyframes RainbowAnimation {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}
@keyframes RainbowAnimation {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}
