import React, { useState } from "react";
import "./menubar.css";
import { useNavigate } from "react-router-dom";

//*  , handleMenubar  ,     setCurrentMenubar   , click "jang" => currentMenubar==="jang"
//*           "    ."
//* ,  json      ,  replacceArray  
//* replaceArray    .
//*    ,
const Menubar: React.FC = () => {
  const navigate = useNavigate();
  const handleMenubar = (menubar) => {
    navigate(`/menubar/${menubar}`);
  };
  const handleCollabo = (collabo) => {
    navigate(`/collabo/${collabo}`);
  };
  return (
    <nav className="menubar-container">
      <ul onClick={() => handleMenubar("jang")}></ul>

      <ul onClick={() => handleMenubar("karina")}></ul>
      <ul className="sparkle" onClick={() => handleCollabo("collabo")}>
        
      </ul>
      <ul onClick={() => handleMenubar("sulyoon")}></ul>
      <ul onClick={() => handleMenubar("yoona")}></ul>
    </nav>
  );
};

export default Menubar;

