import React, { useState, useEffect } from "react";
import useAuthStore from "../../../../JustAnd/GlobalState";
import Confetti from "react-confetti";
import "./renderCompatibility.css";
//*      .
//*   ,   ,    gif C  .
//*  , useEffect  gif s3  URL .
interface ImageData {
  contentType: string;
  src: string;
  data: string;
}
const RenderCompatibilityWithJksy: React.FC = () => {
  const { collaboResult } = useAuthStore((state) => state.collaboResultData);
  const [images, setImages] = useState<ImageData | null>(null);
  const userinfo = collaboResult.split(" ");
  const userNamed = userinfo[1]; //  
  const targetName = userinfo[3]; // ,,,(a,b,c,d)
  const compatibility = userinfo[5]; // ,,,(,,,)
  console.log(userNamed, targetName, compatibility);
  console.log(images, "");
  const [showConfetti, setShowConfetti] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowConfetti(false);
    }, 4000); // 5    

    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(
          `${process.env.REACT_APP_API_URL}/api/sajuRenderImage?type=${compatibility}&targetName=${targetName}`
        );
        const data = await response.json();
        console.log(data, "  .");

        setImages(data);
      } catch (error) {
        console.error(error, "s3   ");
      }
    };
    fetchData();
  }, []);

  return (
    <main className="render-comatibility-box">
      <h1> </h1>
      <h2>
        {showConfetti && <Confetti />}
        {userNamed} {targetName} 
        {compatibility}.
      </h2>
      <div>
        {images && (
          <img
            src={`data:${images.contentType};base64,${images.data}`}
            alt="sajuRenderImage"
          />
        )}
      </div>
    </main>
  );
};

export default RenderCompatibilityWithJksy;
