import React from "react";
import { useState } from "react";
import "./recover.css";
import { useNavigate } from "react-router-dom";

const RecoverUserInfo: React.FC = () => {
  const [userId, setUserId] = useState("");
  const [userEmail, setUserEmail] = useState("");
  const [userEmailForPw, setUserEmailForPw] = useState("");
  const [nonExgistId, setNonExgistId] = useState(false);
  const navigate = useNavigate();

  const handleUserFindId = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/recoverId`,
        {
          method: "POST",
          credentials: "include",
          headers: {
            "Content-type": "application/json",
          },
          body: JSON.stringify({
            inputEmail: userEmail,
          }),
        }
      );

      const data = await response.json();
      console.log(data, "  ");
    } catch (error) {
      console.error(error, "   ");
    }
  };

  const handleUserFindPw = async (e) => {
    e.preventDefault();
    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/recoverPw`,
        {
          method: "POST",
          credentials: "include",
          headers: {
            "Content-type": "application/json",
          },
          body: JSON.stringify({
            inputUserEmail: userEmailForPw,
            inputUserId: userId,
          }),
        }
      );
      if (!response.ok) {
        setNonExgistId(true);
      }
      const data = await response.json();
      console.log(data, "  ");
      if ((data.sucess = true)) {
        navigate("/findUserPw");
      }
    } catch (error) {
      console.error(error, "pw  .");
    }
  };
  return (
    <main className="Recover-section">
      <h1> </h1>
      <div className="Recover-box">
        <section>
          <h2> </h2>
          <form onSubmit={handleUserFindId}>
            <div className="input-group-recover-id">
              <input
                type="email"
                value={userEmail}
                onChange={(e) => setUserEmail(e.target.value)}
                placeholder="  "
              />
              <button className="user-id-button" type="submit">
                 
              </button>
            </div>
          </form>
        </section>
        <section>
          <form onSubmit={handleUserFindPw}>
            <div className="input-group-recover-pw">
              <h2> </h2>
              <input
                type="text"
                value={userId}
                onChange={(e) => setUserId(e.target.value)}
                placeholder=" "
              />
              {nonExgistId && (
                <p className="error-message">
                     .
                </p>
              )}
              <input
                type="email"
                value={userEmailForPw}
                onChange={(e) => setUserEmailForPw(e.target.value)}
                placeholder=" "
              />
              <button className="user-pw-button" type="submit">
                 
              </button>
            </div>
          </form>
        </section>
      </div>
    </main>
  );
};

export default RecoverUserInfo;
