import React from "react";
import { useState, useEffect } from "react";
import validUserPw from "../../services/vaildws";
import { useNavigate } from "react-router-dom";
const FindUserPw: React.FC = () => {
  const [sixCertification, setSixCertification] = useState(""); //   
  const [newPw, setNewPw] = useState(""); //  
  const [verifyNewPw, setVerifyNewPw] = useState(""); //   
  const [leftTime, setLeftTime] = useState(180); //  
  const [isActive, setIsActive] = useState(true); //   
  const [isVerified, setIsVerified] = useState(false);
  const [isPwValid, setIsPwValid] = useState(false);
  const [doPasswordsMatch, setDoPasswordsMatch] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    let interval: any = null;

    if (isActive && leftTime > 0) {
      interval = setInterval(() => {
        setLeftTime(leftTime - 1);
      }, 1000);
    } else if (leftTime === 0) {
      setIsActive(false);
    }

    return () => clearInterval(interval);
  }, [leftTime, isActive]);

  const handleChange = (e) => {
    const value = e.target.value;
    //  ,  6   
    if (value === "" || (value.length <= 6 && /^[0-9]*$/.test(value))) {
      setSixCertification(value);
    }
  };
  //*  
  const handleCertifyNumber = async (e) => {
    e.preventDefault();
    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/certifyNumber`,
        {
          method: "POST",
          credentials: "include",
          headers: {
            "content-type": "application/json",
          },
          body: JSON.stringify({
            inputNumber: sixCertification,
          }),
        }
      );
      const data = await response.json();
      console.log(data, " .");
      if (data.success === true) {
        setIsVerified(true); // => input  
      }
    } catch (error) {
      console.error(error, "  ");
    }
  };
  //*    
  const handleNewPwChange = (e) => {
    const pw = e.target.value;
    setNewPw(pw);
    setIsPwValid(validUserPw(pw)); //   
  };
  //*  
  const handleVerifyNewPwChange = (e) => {
    const pw = e.target.value;
    setVerifyNewPw(pw);
    setDoPasswordsMatch(newPw === pw); //    
  };

  //*  http 
  const handleCertifyPw = async (e) => {
    e.preventDefault();
    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/changePw`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            usernewPw: newPw,
          }),
        }
      );
      const data = await response.json();
      if (data.success === true) {
        navigate("/");
      }
    } catch (error) {
      console.error(error, "    ");
    }
  };
  return (
    <main className="find-user-pw-">
      <div className="find-user-pw-box">
        <h1 className="findUserPwTitle">  </h1>
        <form onSubmit={handleCertifyNumber}>
          <div className="input-group-certify-number">
            <input
              type="text"
              value={sixCertification}
              onChange={handleChange}
              placeholder=" "
              required
            />
            <button
              type="submit"
              className="submit-btnn"
              disabled={!isActive || isVerified}
            >
               
            </button>
          </div>
        </form>
        {isVerified ? (
          <p> </p>
        ) : isActive ? (
          <p> : {leftTime}</p>
        ) : (
          <p> </p>
        )}
        {isVerified && (
          <form onSubmit={handleCertifyPw}>
            <div className="input-group-new-pw">
              <input
                type="password"
                value={newPw}
                onChange={handleNewPwChange}
                placeholder="  "
                required
              />
              {isPwValid ? (
                <span style={{ color: "green" }}> .</span>
              ) : null}
              <input
                type="password"
                value={verifyNewPw}
                onChange={handleVerifyNewPwChange}
                placeholder="  "
                required
              />
              {doPasswordsMatch ? (
                <span style={{ color: "green" }}> .</span>
              ) : null}
              <button
                type="submit"
                disabled={!isPwValid || !doPasswordsMatch}
                className="submit-btnn"
              >
                 
              </button>
            </div>
          </form>
        )}
        <input type="file"></input>
      </div>
    </main>
  );
};

export default FindUserPw;
