___CSS_LOADER_EXPORT___.push([module.id, `/* menubar.css */
.menubar-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr); /* 4 ,    */
  grid-gap: 10px; /*     */
  list-style: none; /*     */
  padding: 0; /*    */
}

.menubar-container > ul {
  background-color: #42d538; /*  */
  padding: 10px; /*  */
  text-align: center; /*    */
  border: 1px solid #ddd; /*  */
  cursor: pointer; /*      */
  transition: background-color 0.3s; /*    */
}

.menubar-container > ul:hover {
  background-color: #e0e0e0; /*      */
}

.button {
  background-color: lightgray; /*   */
  color: black; /*   */
  border-radius: 30px; /*    */
  /*     */
}

.button:hover {
  background-color: gray; /*      */
}
.sparkle {
  position: relative;
  overflow: hidden;
}

.sparkle::before {
  content: "";
  position: absolute;
  top: 0;
  left: -75%;
  width: 50%;
  height: 100%;
  background: gold;
  transform: skewX(-25deg);
  transition: left 0.5s ease-in-out;
}

.sparkle:hover::before {
  left: 125%;
}
