  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4  */
  grid-template-rows: repeat(4, 1fr); /* 4  */
  grid-gap: 50px; /*    */
  width: 100%;
  height: 100%;
  justify-content: center;
}

.content {
  width: 100%;
  height: 100%;
  border: 1px solid #ddd; /*  */
  color: black; /*   */
  border-radius: 5px; /*   */
  list-style: none; /* li     */
  padding: 10px; /*   */
}

.mainThumbNail {
  width: 100%;
  height: auto; /*    */
  border-radius: 5px; /*    */
}

