import React, { useState } from "react";
import pageLogo from "../../assets/resource/pagelogo.png";
import "./header.css";
import { useNavigate } from "react-router-dom";
import useAuthStore from "../../JustAnd/GlobalState";

const Header: React.FC = () => {
  const Navigate = useNavigate();
  const { setAllertMessage, showAlertMessage, setConfirmAction, hideAlert } =
    useAuthStore((state) => state.alertState);
  const { jwtDecodingData } = useAuthStore((state) => state.jwtGlobal);
  const [isLoading, setIsLoading] = useState(false);
  const [isLoaded, setIsLoaded] = useState(false);

  const fetchData = async () => {
    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/auth/clearCookie`,
        {
          method: "POST",
          credentials: "include",
        }
      );
      if (!response.ok) {
        throw new Error("    ");
      }
      const data = await response.json();
      console.log("  data:", data.status);
      // if (data.message === "  .") {
      //   setAllertMessage("  ?");
      //   showAlertMessage();
      //   setConfirmAction(() => {
      //     Navigate("/");
      //   });
      // }
      console.log(" :  .");
    } catch (error) {
      console.error("  :", error);
    }
  };

  const gotoPage = () => {
    if (jwtDecodingData) {
      Navigate("/myPage");
    } else {
      setAllertMessage(
        "    .  ?"
      );
      showAlertMessage();
      setConfirmAction(() => {
        hideAlert();
        Navigate("/signUp");
      });
    }
  };
  return (
    <header className="header">
      {/* {isLoading && <LoadingModalViewComponent />}
      <button className="mypage-btn" onClick={handlebing}>
         
      </button> */}
      <button
        className="myPage-btn"
        onClick={() => {
          gotoPage();
        }}
      >
        
      </button>
      <img
        className="logo"
        onClick={() => {
          Navigate("/");
        }}
        src={pageLogo}
        alt="Page Logo"
      />
      {!jwtDecodingData ? (
        <button
          className="loginButton"
          onClick={() => {
            console.log("login  ");
            Navigate("SignUp");
          }}
        >
          
        </button>
      ) : (
        <button
          className="logoutButton"
          onClick={() => {
            setAllertMessage("  ?");
            showAlertMessage();
            setConfirmAction(() => {
              fetchData();
              hideAlert();
              Navigate("/signUp");
            });
          }}
        >
          
        </button>
      )}
      <h1 className="title"></h1>
    </header>
  );
};

export default Header;
