import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { karinaData, AuthContextType } from "../../types/contentType";
import "./writePage.css";
import { v4 as uuidv4 } from "uuid";
import imageCompression from "browser-image-compression";
import useAuthStore from "../../JustAnd/GlobalState";

//  , MaintentsProps  , prop .
// addToArray , obj  , obj karinadata.
interface Errors {
  title?: string;
  menubar?: string;
  photoSumnail?: string;
  photos?: string;
}
const WritePage: React.FC = () => {
  const navigate = useNavigate();
  const myUUID = uuidv4();
  const [title, setTitle] = useState("");
  const [menubar, setMenubar] = useState(""); //  
  const [photoSumnail, setPhotoSumnail] = useState(""); //   URL 
  const [photos, setPhotos] = useState<string[]>([]);
  console.log(photos, "   ");
  const [compressedFile, setCompressedPhotoSumnail] = useState<File | null>(
    null
  );
  const [compressedImages, setCompressedImages] = useState<File[]>([]);
  const { jwtDecodingData } = useAuthStore((state) => state.jwtGlobal);
  const [errors, setErrors] = useState<Errors>({});
  const [isValiable, setIsValiable] = useState(true);
  console.log(compressedImages, " blob  !!");
  const validateForm = () => {
    const newErrors: Errors = {};
    if (!title.trim()) newErrors.title = " ";
    if (!menubar.trim()) newErrors.menubar = " ";
    if (!photoSumnail) newErrors.photoSumnail = " ";
    if (photos.length === 0) newErrors.photos = " ";
    return newErrors;
  };
  const handleRemove = () => {
    setIsValiable(false);
  };
  const handlePhotoSumnailChange = async (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    if (event.target.files && event.target.files[0]) {
      const originalFile = event.target.files[0];

      //    
      const options = {
        maxSizeMB: 0.5, //   
        maxWidthOrHeight: 1280, //    
        useWebWorker: true,
      };

      try {
        //  
        const compressedFile = await imageCompression(originalFile, options);

        //   URL   
        setPhotoSumnail(URL.createObjectURL(compressedFile));
        console.log(" !!");

        //        
        setCompressedPhotoSumnail(compressedFile);
        console.log(compressedFile, " ");
      } catch (error) {
        console.error("   :", error);
      }
    }
  };
  //    
  const handlePhotosChange = async (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    if (event.target.files) {
      const files = Array.from(event.target.files);
      const options = {
        maxSizeMB: 0.5,
        maxWidthOrHeight: 1280,
        useWebWorker: true,
      };
      console.log("imageCompresiion Check");
      try {
        //     Blob URL 
        const compressedFiles = await Promise.all(
          files.map((file) => imageCompression(file, options))
        );
        const newFileUrls = compressedFiles.map((file) =>
          URL.createObjectURL(file)
        );
        setPhotos((prevPhotos) => [...prevPhotos, ...newFileUrls]);
        setCompressedImages((prevCompressedImages) => [
          ...prevCompressedImages,
          ...compressedFiles,
        ]);
        if (photos.length + newFileUrls.length > 6) {
          alert(" 6    .");
          setPhotos((prevPhotos) =>
            [...prevPhotos, ...newFileUrls].slice(0, 6)
          );
        }
      } catch (error) {
        console.error("   :", error);
      }
    }
  };

  //*   => addToArray   =>  
  const handleSubmit = async (e) => {
    e.preventDefault();
    const formErrors = validateForm();
    if (Object.keys(formErrors).length > 0) {
      setErrors(formErrors);
      return;
    }
    const newKarinaData: karinaData = {
      uuid: myUUID,
      menubar: menubar,
      title: title,
      photosumnail: photoSumnail,
      photos: photos, //     .
    };
    console.log(newKarinaData);
    const formData = new FormData();

    if (compressedFile) {
      formData.append("photoSumnail", compressedFile, compressedFile.name);
    }
    compressedImages.forEach((file) => {
      formData.append("photos", file, file.name);
    });
    formData.append("id", myUUID);
    formData.append("menubar", menubar);
    formData.append("title", title);
    const object = {};
    formData.forEach((value, key) => {
      object[key] = value;
    });
    console.log(object, " .");

    //   URL
    const url = `${process.env.REACT_APP_API_URL}/api/upload?`;

    try {
      // fetch API   POST  
      const response = await fetch(url, {
        method: "POST",
        body: formData,

        headers: {
          Authorization: `${jwtDecodingData?.["token"]}`,
        },
      });

      if (!response.ok) throw new Error(`Error: ${response.status}`);
      const responseData = await response.json();
      console.log(responseData.data.menubar);

      navigate("/");
    } catch (error) {
      console.error("Error sending data to the server:", error);
      //   
    }
  };

  return (
    <div className="write-container">
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          className="title-input"
          placeholder=""
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        .
        <select
          className="menubar-select"
          value={menubar}
          onChange={(e) => {
            const newMenubar = e.target.value;
            setMenubar(newMenubar);
          }}
        >
          <option value="" disabled selected>
             
          </option>
          <option value="jang"></option>
          <option value="karina"></option>
          <option value="sulyoon"></option>
          <option value="yoona"></option>
        </select>
        <input
          type="file"
          className="photo-thumbnail-input"
          onChange={handlePhotoSumnailChange}
        />
        <div className="photos-Thumnail-preview">
          {isValiable && (
            <div className="thumbnail-container">
              <img
                src={photoSumnail}
                className="thumbnail-image"
                alt="thumbnail"
              />
              <button className="close-button" onClick={handleRemove}>
                X
              </button>
            </div>
          )}
        </div>
        <input
          type="file"
          className="photos-input"
          multiple
          onChange={handlePhotosChange}
        />
        {/*      */}
        <div className="photos-preview">
          {photos.map((photoUrl, index) => (
            <img
              key={index}
              src={photoUrl}
              alt={`  ${index + 1}`}
              className="photo-thumbnail"
            />
          ))}
        </div>
        <button type="submit" className="submit-button">
          
        </button>
      </form>
    </div>
  );
};

export default WritePage;
